.products {
  display: block;
  width: 100%;
  padding: 0;
  margin: 2rem 0;

  .productResults {
    display: flex;
    flex-wrap: wrap;
    margin: 3rem -10px 0;
  }
}

.product {
  width: 33.333333333333333%;
  margin: 0 auto 2rem;
  padding: 0 10px;
  float: left;

  .thumb {
    display: block;
    width: 100%;

    img {
      display: block;
      width: 420px;
      height: 408px;
      margin: 0;
    }
  }

  .details {
    display: block;
    width: 100%;
    padding: 1rem 0;
    margin: 0 auto;

    ul,
    ul li {
      padding: 0;
      margin: 0;
    }

    ul {
      li {
        display: block;
        width: 100%;
        list-style-type: none;
        text-align: left;
        margin: 0 0 0.5rem;

        a {
          color: black;
        }

        .name {
          font-size: 2.2rem;
          line-height: 1.2;
          font-weight: 400;
        }

        .price {
          font-size: 1.6rem;
          line-height: 1;
          font-weight: 400;
        }

        .addToCart {
          margin: 2rem 0 0 0;
        }
      }
    }
  }
}
