.admin {
  padding: 0 10px;

  .callToActions {
    display: inline-block;
    width: 100%;
    padding: 0;
    margin: 0 auto;

    ul,
    li {
      margin: 0;
      padding: 0;
      list-style-type: none;
    }

    ul {
      li {
        display: inline-block;
      }
    }
  }
}

.manageProducts {
  h1 {
    margin-top: 1.5rem;
  }

  table.results {
    tr {
      &:nth-child(even) {
        background-color: #d3d3d3;
      }

      .thumb {
        float: left;
        width: 100px;
        height: 100px;
        object-fit: cover;
      }
    }
  }
}
